
/**
 * Sogou support
 */
import Native from './native'
import { Apps, OS } from '../constants'

export default class Sogou extends Native {
  constructor (context) {
    super(context)
  }
  static appMap = {
    [Apps.WECHAT]: true,
    [Apps.MOMENTS]: true,
    [Apps.QQ]: true,
    [Apps.QZONE]: true,
    [Apps.WEIBO]: true
  }
  static isSupport (context, appName) {
    return undefined !== Sogou.appMap[appName] &&
      OS.IOS === context.osName
  }
  share (appName) {
    const dataSogouNeed = {
      shareTitle: this.shareData.title,
      shareContent: this.shareData.desc,
      shareImageUrl: this.shareData.desc,
      shareUrl: this.shareData.link
    }
    SogouMse.Utility.shareWithInfo(dataSogouNeed)
  }
}



